import { Component } from 'apprun';
export default class extends Component {
    constructor() {
        super(...arguments);
        this.view = _ => `_html:<h1 id="layout---do-it-yourself">Layout - Do It Yourself</h1>
<p>Instead of asking you to follow the configuration or convention, sometimes it is easier to create your own layout.</p>
<p>You can create the <strong>src/index.tsx</strong> of your own without using the <strong>app.start</strong> function.</p>
<p>In this case, you will need to know how to create the layout and pages in the <strong>src/index.tsx</strong> page.</p>
<h2 id="create-layout">Create Layout</h2>
<p>You can create the layout as a regular <a href="#component">component</a> and render it before rendering pages. You can set up title, menus, and nav whatever you want freely. A layout template looks like:</p>
<pre><code class="language-javascript">import app from 'apprun';

export default ({ title, element, nav, sidebar }) =&gt; &lt;&gt;
  &lt;div id=&quot;main&quot;&gt;&lt;div&gt;
&lt;/&gt;
</code></pre>
<h2 id="create-pages">Create Pages</h2>
<p>The auto-generated event-component mapping in <strong>src/_lib/index.tsx</strong> makes it very easy to create the pages, and sets the routing events to the pages in a generic way.</p>
<pre><code class="language-javascript">const element = 'main';
pages.forEach(def =&gt; {
  const [e, Comp] = def;
  const component = new Comp().mount(element);
  app.on(e, (...p) =&gt; component.run('.', ...p));
});
</code></pre>
<blockquote>
<p>Note: the . event is the <em>refresh</em> event built-in every AppRun component. It makes components render themselves.</p>
</blockquote>
<h2 id="your-own-index-file">Your Own Index File</h2>
<p>Put them together a simple index page looks like:</p>
<pre><code class="language-javascript">import app from 'apprun';
import layout from './my-layout';
import pages from './_lib';

app.render(document.body, &lt;layout /&gt;);
const element = 'main';
pages.forEach(def =&gt; {
  const [e, Comp] = def;
  const component = new Comp().mount(element);
  app.on(e, (...p) =&gt; component.run('.', ...p));
});
</code></pre>
<p>Next, you can learn more about the <a href="#pages">pages</a>.</p>
`;
    }
}
